// @mui
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import Typography from "@mui/material/Typography";
// components
import { useEffect, useState } from "react";
// eslint-disable-next-line import/no-extraneous-dependencies
import dayjs from "dayjs";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
// eslint-disable-next-line import/no-extraneous-dependencies
import { toast } from "react-toastify";
import {
  GetBalanceSheetByMonth,
  GetCOA,
  GetRevenueByMonth,
} from "src/api/transaction";
// eslint-disable-next-line import/no-extraneous-dependencies
import { useSettingsContext } from "src/components/settings";
import { useAuthContext } from "src/auth/hooks";
import AnalyticsCurrentVisits from "./analytics-current-visits";
import AnalyticsCard from "./analytics-card";
import { handleValue } from "./func";

export default function IncomeStatement() {
  const settings = useSettingsContext();
  const [result, setResult] = useState({
    Revenue: null,
    Expense: null,
    TotalRevenue: [],
    NetIncome: [],
    GrossProfit: [],
    CashFlow:[]
  });
  const [coa, setCoa] = useState([]);
  const [selectedDate, setSelectedDate] = useState({ from: null, to: null });
  const [error, setError] = useState(null);
  const { user } = useAuthContext();

  const handleDateChange = (date, type) => {
    let dateResult;
    if (type) {
      dateResult = { ...selectedDate, from: date };
    } else {
      dateResult = { ...selectedDate, to: date };
    }
    setSelectedDate(dateResult);
  };

  useEffect(() => {
    const fetchTransaction = async () => {
      try {
        if (selectedDate.from !== null && selectedDate.to !== null) {
          const startDate =
            dayjs(selectedDate.from).year() * 100 +
            dayjs(selectedDate.from).month();
          const endDate =
            dayjs(selectedDate.to).year() * 100 +
            dayjs(selectedDate.to).month();
          const dateRange = { from: startDate, to: endDate };

          const data =
            user.role === 1
              ? {
                  userId: user._id,
                  date: dateRange,
                }
              : {
                  userId: user.parent,
                  date: dateRange,
                };

          const tempResult = [];
          const response = await GetRevenueByMonth(data);
          if (response.type === "success") {
            toast.success("Revenue Successfully Upload!", { theme: "colored" });
            const dataResult = response.data.data;
            dataResult.sort((a, b) => a.dateFlag - b.dateFlag);
            dataResult.map((element) => {
              tempResult.push({
                dateFlag: element.dateFlag,
                data: element.data,
              });
              return true;
            });
            setError(null);
          } else {
            toast.error("Revenue Upload Error", { theme: "colored" });
          }
          const cash = await GetBalanceSheetByMonth(data);
          if (cash.type === "success") {
            toast.success("Balance Sheet Successfully Upload!", {
              theme: "colored",
            });
            const dataResult = cash.data.data;
            dataResult.sort((a, b) => a.dateFlag - b.dateFlag);
            dataResult.map((element) => {
              tempResult.map((item) => {
                if (item.dateFlag === element.dateFlag) {
                  item.data = [...item.data, ...element.data];
                }
                return true;
              });
              return true;
            });
          } else {
            toast.error("Balance Sheet Upload err", { theme: "colored" });
          }
          const summarize = handleValue(tempResult, coa);
          setResult(summarize);
        }
      } catch (err) {
        setError(err);
      }
    };
    fetchTransaction();
  }, [selectedDate, user._id, user.parent, user.role, coa]);

  useEffect(() => {
    const fetchCOA = async () => {
      const data =
        user.role === 1 ? { userId: user._id } : { userId: user.parent };
      const response = await GetCOA(data);
      if (response.type === "success") {
        toast.success("COA Successfully Upload!", { theme: "colored" });
        setCoa(response.data);
      } else {
        toast.error("COA Upload Error", { theme: "colored" });
      }
    };
    fetchCOA();
  }, [user._id, user.parent, user.role]);

  return (
    <Container maxWidth={settings.themeStretch ? false : "xl"}>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          pb: 2,
          flexDirection: { xs: "column", sm: "row" },
        }}
      >
        <Typography variant="h4">ðŸ‘‹ Welcome to Dashboard</Typography>
        <Box
          sx={{
            display: "flex",
            justifyContent: "flex-end",
            alignItems: "center",
            mr: 4,
            border: "2px solid",
            borderColor: "#007867",
            borderRadius: 1,
            p: 0.2,
          }}
        >
          <DatePicker
            label={selectedDate.from === "" ? "From" : ""}
            views={["month", "year"]}
            value={selectedDate.from}
            onChange={(e) => handleDateChange(e, true)}
          />
          <Box sx={{ mx: 1 }}>-</Box>
          <DatePicker
            label={selectedDate.to === "" ? "To" : ""}
            views={["month", "year"]}
            value={selectedDate.to}
            onChange={(e) => handleDateChange(e, false)}
          />
        </Box>
      </Box>

      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          p: 2,
          flexDirection: { xs: "column", sm: "row" },
        }}
      >
        <Box sx={{ width: { xl: "50%", lg: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCard
            title="Total Revenue"
            data={result?.TotalRevenue ?? []}
          />
        </Box>
        <Box sx={{ width: { xl: "50%", lg: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCard
            title="Cash Flow"
            data={result?.CashFlow ?? []}
          />
        </Box>
      </Box>

      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          p: 2,
          flexDirection: { xs: "column", sm: "row" },
        }}
      >
        <Box sx={{ width: { xl: "50%", lg: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCard title="NetIncome" data={result?.NetIncome ?? []} />
        </Box>
        <Box sx={{ width: { xl: "50%", lg: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCard
            title="Gross Profit"
            data={result?.GrossProfit ?? []}
          />
        </Box>
      </Box>

      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          p: 2,
          flexDirection: { xs: "column", sm: "row" },
        }}
      >
        <Box sx={{ width: { xl: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCurrentVisits
            title="Revenue Distribtion"
            chart={{
              series: result?.Revenue ?? [
                { name: "a", value: 1 },
                { name: "b", value: 1 },
                { name: "c", value: 1 },
                { name: "d", value: 1 },
                { name: "e", value: 1 },
              ],
            }}
          />
        </Box>
        <Box sx={{ width: { xl: "50%", xs: "95%" }, p: 1 }}>
          <AnalyticsCurrentVisits
            title="Expense Distribtion"
            chart={{
              series: result?.Cash ?? [
                { name: "a", value: 1 },
                { name: "b", value: 2 },
                { name: "c", value: 3 },
                { name: "d", value: 2 },
              ],
            }}
          />
        </Box>
      </Box>
      {error && (
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "60vh", // Ensure full viewport height
            textAlign: "center",
          }}
        >
          <Typography variant="h5" sx={{ pt: 5 }}>
            {error?.message}
          </Typography>
        </Box>
      )}
    </Container>
  );
}
